/* CSS Variables */
@import "rvariables.scss";
/* Root defaults */
/* Toolbar */
.css-hyum1k-MuiToolbar-root {
  min-height: auto !important;
  padding: 0 !important;
}
/* Card Actions */
.css-dnrpxu-MuiCardActions-root {
  min-height: auto !important;
  padding: 0 !important;
}
/* Link */
a {
  min-width: auto !important;
  padding: 0 !important;
}
/* Horizontal line styles */
hr {
  border: 1px inset rgba(171, 150, 111, 0.5);
  margin: 5vw;
  width: 100%;
  /* Make horizontal line invisible */
  &.space {
    margin: 2.5vw;
    opacity: 0;
  }
  /* Make horizontal line as appbar link mediator */
  &.mediator {
    width: 100%;
    opacity: 0;
    margin: 0;
  }
}
/* HMTL element */
html {
  background-image: url('../../public/assets/image/background/site-bg.webp');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  /* Body element */
  body {
    background: linear-gradient(
        rgba(0, 0, 0, $--bg-darker-1),
        rgba(0, 0, 0, $--bg-darker-1)
      ),
      rgba(65, 65, 65, 0.65);
    justify-content: center;
    min-width: 400px;
    margin: 0;
  }
  /* Body class*/
  .body {
    justify-content: center;
    /* Side Margin: Margin - Body - Margin */
    &Margin {
      border: none;
    }
    /* Upper body element. Content area + Left and Right columns */
    &Column {
      border-right: 1.5px double rgb(155, 155, 155);
      border-left: 1.5px double rgb(155, 155, 155);
      background: rgba(65, 65, 65, 0.35);
      min-height: calc(
        100vh - $--navigation-height - $--top-height - $--bottom-height -
          $--footer-height
      );
      position: relative;
      max-width: 1920px;
      padding: 0;
      /* Content area */
      .bodyContent {
        justify-content: flex-start;
        flex-direction: column;
        align-items: center;
        flex-wrap: nowrap;
        display: flex;
        width: 100%;
        /* Edit mode */
        &Edit {
          @extend .bodyContent;
          opacity: 0.6;
        }
        /* Page Header */
        img.header {
          height: calc(360px - $--navigation-height - $--top-height);
          margin: 0 0 calc((3vh + 17px) * -1) 0 !important;
          border: 5px ridge rgba(171, 150, 111, 1);
          object-position: 20% 100%;
          object-fit: cover;
          border-radius: 0;
          width: 100%;
        }
        video.header {
          height: calc(360px - $--navigation-height - $--top-height);
          border: 5px ridge rgba(171, 150, 111, 1);
          margin: 0 0 -2.5px 0 !important;
          object-position: 50% 15%;
          object-fit: cover;
          border-radius: 0;
          z-index: 1049;
          width: 100%;
        }
        /* Top Right Corner Menu */
        .dotsHeader {
          position: absolute;
          z-index: 1050;
          height: 25px;
          width: 25px;
          right: 10px;
          top: 5px;
          /* Menu Icon */
          & svg {
            filter: $--svg-summer-sun-mild;
            height: 25px;
            width: 25px;
            /* Hover */
            &:hover {
              filter: $--svg-summer-sun-dark;
              cursor: pointer;
            }
          }
          /**/
          & div.menu {
            border: 1.5px double rgba(155, 155, 155, 1);
            background: rgba(65, 65, 65, 1);
            position: absolute;
            right: 4.165px;
            top: 16.67px;
            padding: 0;
            /**/
            & button {
              min-width: min-content;
              padding: 9px 12px;
              &:hover {
                background: rgb(87, 87, 87);
              }
              /**/
              & > svg {
                filter: $--svg-winter-wonderland-dark;
                margin: 0 5px 0 -5px;
                min-width: 15px;
                max-width: 25px;
                height: 100%;
                width: 1vw;
                &:hover {
                  filter: $--svg-winter-wonderland-soft;
                }
              }
            }
          }
        }
        /* Top Settings For Content */
        & .content {
          justify-content: center;
          align-items: center;
          display: flex;
          width: 100%;
          /* ContentColumn. Content Area Whole Page Height */
          &Column {
            @extend .content;
            flex-direction: column;
          }
          /* ContentRow. Content Area For Row Of Elements */
          &Row {
            @extend .content;
            margin: 0 !important;
            /* Editing Toolbar */
            & > div.toolbar {
              border: 1px solid rgba(155, 155, 155, 1);
              height: 30px;
              width: 100%;
              /* Editing Toolbar Icons */
              & > svg {
                filter: $--svg-gold-royal-dark;
                padding: 0 5px !important;
                height: 25px;
                width: 25px;
                /* Hover */
                &:hover {
                  filter: $--svg-gold-royal-soft;
                  cursor: pointer;
                }
              }
            }
            /* ContentRow Title Icon */
            & > h1 > svg {
              height: calc(max(1.6666666667vw, 22px) * 1.167) !important;
              width: calc(max(1.6666666667vw, 22px) * 1.167) !important;
              filter: $--svg-summer-sun-mild;
              position: absolute;
              /* Hover */
              &:hover {
                filter: $--svg-summer-sun-dark;
                cursor: pointer;
              }
            }
            /* Collapse Area. Element In ContentRow  */
            .collapseArea {
              & > div > div > div {
                /**/
                &.Edit > div {
                  /**/
                  &:hover {
                    background-color: rgba(65, 65, 65, 0.65);
                  }
                }
                & > div {
                  @extend .content;
                  //max-width: calc(10*(100vw/12)/2 - 10vw);
                  position: relative;
                  margin: 2vh 0;
                  /* Link Cards */
                  &.card {
                    margin: 2vh 1vh;
                  }
                  /* Media Element In ContentRow */
                  & {
                    /* Image Element In ContentRow */
                    & > img {
                      border: 2.5px ridge rgba(171, 150, 111, 1);
                      object-position: 20% 100%;
                      width: calc(100% - 5vw);
                      border-radius: 10px;
                      object-fit: cover;
                      margin: 0 2.5vw;
                    }
                    /* Video Element In ContentRow */
                    & > video {
                      border: 2.5px ridge rgba(171, 150, 111, 1);
                      object-position: 20% 100%;
                      width: calc(100% - 5vw);
                      border-radius: 10px;
                      object-fit: cover;
                      margin: 0 2.5vw;
                    }
                    /**/
                    & > svg {
                      filter: $--svg-orange-crushed-dark;
                      position: absolute;
                      height: 15%;
                      width: 15%;
                      /**/
                      &:hover {
                        filter: $--svg-orange-crushed-soft;
                        cursor: pointer;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
