/* H6: Other */
h6 {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  border-bottom: 2px double rgba(171, 150, 111, 1);
  border-top: 1px double rgba(171, 150, 111, 1);
  line-height: $--navigation-height;
  color: rgba(255, 255, 255, 1);
  padding: 0 5px 0 0 !important;
  font-weight: 900 !important;
  margin: 0 0 0 0 !important;
  text-transform: uppercase;
  text-align: center;
  /* Brand name */
  &.h0 {
    text-shadow: 1px 11px 6px rgba(16, 16, 16, 0.4),
      1px 12px 10px rgba(16, 16, 16, 0.2), 1px 13px 35px rgba(16, 16, 16, 0.4),
      1px 14px 60px rgba(16, 16, 16, 0.6);
    text-align: left;
    border: none;
  }
  /* Page Menu */
  &.h1 {
    font-size: max(calc(100vw / 200), 10px);
    padding: 0 !important;
    line-height: normal;
    border: none;
  }
